@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600&display=swap");

:root {
    --dark-red: #da4453;
    --light-red: #ed5565;
    --dark-green: #8cc152;
    --light-green: #a0d468;
    --dark-aqua: #3bafda;
    --light-aqua: #4fc1e9;
    --dark-blue: #4a89dc;
    --light-blue: #5d9cec;
    --dark-white: #e6e9ed;
    --light-white: #f5f7fa;
    --dark-gray: #aab2bd;
    --light-gray: #ccd1d9;
    --dark-black: #434a54;
    --light-black: #656d78;
    --color-black: #355c7d;
    --color-purple: #6c5b7b;
    --color-pink: #c06c84;
    --color-red: #f67280;
    --color-orange: #f8b195;
}

html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    margin: 0;
    margin: 0;
    font-family: "Source Sans Pro", sans-serif;
}
